import React, { PureComponent } from "react";
import { isMobile } from "react-device-detect";
import { connect } from "react-redux";
import Card from "./Elements/Card";
import { getDataNextSteps } from "../../../actions/themeActions"; 
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, FreeMode, Mousewheel, Navigation, Grid} from 'swiper/modules';
import 'swiper/css/bundle';
import { withData } from "../Elements/Observer";
import { getPersonalize } from "../../../utils";

const SilderCom = (props) => {
  const { inputRefs, inputName, themeId, row, numberOfCoumn, manageItemCount, slideDuration, mobileColumn, secondSildeData, trimContent, firstSildeData, completeDataObject, themeHeight, wall, postSizeInPx, clickToShowPopUp, onClickToCTA, type, isDisplay, postData, intractiveData} = props;

  const arr = type === 2 && themeId == 71 && isDisplay && row == 2 ? [1, 2] : [1];

  return (
    <>
      {
       arr.map((itme, index) => {
        const data = index == 0 ? firstSildeData :  secondSildeData

         return <Swiper
            key={index}
            id={index == 0 && "swiper-row"}
            ref={(parentID) => inputRefs[inputName] = parentID}
            style={{ width: '100%' }}
            navigation={{ nextEl: ".arrow-right", prevEl: ".arrow-left" }}
            speed={ isDisplay && themeId == 71 ? 8000 : 1000}
            loop={wall.product && themeId == 71 ? false : true }
            grid= {{ rows : !isDisplay && themeId == 71  && row == 2 && postData.length > 15 ? 2 : 1 } }
            edgeSwipeDetection={true}
            slidesPerView={numberOfCoumn > 0 ? numberOfCoumn : manageItemCount}
            allowTouchMove={isDisplay ? false : true}
            passiveListeners={true}
            autoplay={{ delay: themeId == 71 ? 1 : slideDuration ? slideDuration * 1000 : 5000, disableOnInteraction: false, reverseDirection: index == 0 ? false : true}}
            breakpoints={{
              320:{
                slidesPerView: mobileColumn > 0 ? mobileColumn : 1,
                perMove: mobileColumn > 0 ? mobileColumn : 1,
              },
              560: {
                slidesPerView: mobileColumn > 0 ? mobileColumn : 1,
                perMove: mobileColumn > 0 ? mobileColumn : 1,
              },
              767: {
                slidesPerView: numberOfCoumn > 0 ? numberOfCoumn - 3 : manageItemCount,
                perMove: numberOfCoumn > 0 ? numberOfCoumn - 3 : manageItemCount,
              },
              991: {
                slidesPerView: numberOfCoumn > 0 ? numberOfCoumn : manageItemCount,
                perMove: numberOfCoumn > 0 ? numberOfCoumn : manageItemCount,
              }
            }}
             modules={isDisplay ? [Autoplay, Mousewheel, FreeMode] : [Mousewheel, FreeMode, Navigation, Grid]}
            className={`tb_fc_post_slider splide2 ${trimContent ? "tb_fc_fixed_height" : ""}`}>
            {data && data.length > 0 &&
              data.map((item, index) => {
              const cardData = completeDataObject[item];
              const HocCard = !wall.isDisplay ? Card : withData(Card);

              return (
                (cardData.file || themeId == 70) &&  
                  <SwiperSlide style={{ margin: 0, padding: 0, height : row==2 ? `calc(${100/row}vh - ${themeHeight/2}px)` : '100%'  }} key={`splide${index}`} >
                       <HocCard
                      wall={wall}
                        itemData={cardData}
                        key={`hr_${index}`}
                        themeHeight={themeHeight}
                        row={row}
                        itemIndex={index}
                        clickToShowPopUp={clickToShowPopUp}
                        onClickToCTA={onClickToCTA}
                        postSizeInPx={postSizeInPx}
                        intractiveData={intractiveData}
                      />
              </SwiperSlide>
              );
            })}
          </Swiper>
       }
        )
      }
    </>
  )
}

class FallingColumns extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { parent_id: "", checkextraHeight: this.props.isDisplay ? 0 : 1, wHeight: 0 };
    this.inputRefs = {};
  }
  

  componentDidMount(){
    const {isDisplay} = this.props
    this.updatethemHeight()
      if(isDisplay){
        window.addEventListener("resize", () => { this.updatethemHeight(); this.props.heightUpdate() })
      }
  }

  updatethemHeight(){
      this.props.heightUpdate()
      this.setState({ wHeight: window.innerHeight })
      setTimeout(() => this.setState({checkextraHeight : true }) , 500);
  }

  componentWillMount() {
    const { renderId } = this.props.renderId;
    this.setState({
      windowWidth: renderId
        ? document.getElementById(renderId).clientWidth
        : window.innerWidth,
    });
    
     if(this.props.isDisplay) setTimeout(() => this.setState({wHeight : Math.random()}), 500)
  }

  
  requestData = () => {
      const { appendData, wall, preRender, hasMoreData } = this.props;
      if(!getPersonalize()){
        if (hasMoreData) {
          this.props.getDataNextSteps(
            wall.Wall.id,
            Math.floor(Date.now() / 1000),
            wall.ThemeRule.numberOfPosts,
            appendData.networkID,
            appendData.after,
            preRender,
            appendData.heightEvent
          );
        }
    }
  }; 
  
  componentDidUpdate(prev, prevState) {
    const {wall} = this.props

     if(wall.product==0) this.updatethemHeight() 

     if(wall.isEditor && (this.props.themeUpdate!=prev.themeUpdate || wall.product!= prev.wall.product || wall.id!= prev.wall.id || wall.Banner.bannerUpdate!= prev.wall.Banner.bannerUpdate || wall.Banner.banner_position!=prev.wall.Banner.banner_position || wall.BannerImage.status!=prev.wall.BannerImage.status)) this.updatethemHeight() 
  }

  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, hasMoreData, onClickToCTA, parentID, postSizeInPx, themeHeight, intractiveData } = this.props;
    const { isDisplay, ThemeRule, Personalization } = wall  

    const { checkextraHeight, wHeight } = this.state;
    const { themeId, numberOfCoumn, mobileColumn } = ThemeRule;
    const slideDuration = Personalization.slideDelay == 10 ? 5 : Personalization.slideDelay;
    const trimContent = ThemeRule.trimcontent ? true : false;
    const isMobileStatus = window.innerWidth < 768 || isMobile ? true : false;
    const manageItemCount = postData && postData.length ? isMobileStatus ? 1 : postData.length > 3 ? 3 : postData.length : 3;
    const inputName = parentID;

    const row = ThemeRule.themeId == 71 ? ThemeRule.multiRow ? ThemeRule.multiRow : 2 : 1;

    const midIndex = Math.floor( postData.postData.length > 0 ? postData.postData.length / 2 : [] );
    let firstSildeData = themeId == 71 && row == 2 && isDisplay && postData.postData.length > 15 ? postData.postData && postData.postData.length > 0 &&   postData.postData.slice(0, midIndex) : postData.postData;
    let secondSildeData = postData.postData && postData.postData.length > 15 && postData.postData.slice(midIndex);

    if(numberOfCoumn >= firstSildeData.length){
      const num = (numberOfCoumn - firstSildeData.length + 1)
      for(let i=0; i <= num; i++){
        firstSildeData = [...firstSildeData, firstSildeData[i]]
      }
    }

    if(numberOfCoumn >= secondSildeData.length && themeId == 71 && row == 2 ){
      const num = (numberOfCoumn - secondSildeData.length + 1)
      for(let i=0; i < num; i++){
        secondSildeData = [...secondSildeData, secondSildeData[i]]
      }
    }
    
    if(numberOfCoumn)
    var topMargin = 0;
    const top_position = document.querySelector(".onsiteupload_top_position");
    if (top_position)  topMargin = top_position.clientHeight + 10

    return (
  
    checkextraHeight  ? 
     <div className="horizontal_slider_relative" style={{ height : (row == 1) && themeId == 71 || postData.postData.length < 15 ? '' : `calc(100vh - ${(themeHeight)}px)`  }}>
       <div key={themeHeight} id={`${ themeId == 70 ? "horizontal" : themeId == 71 ? "motion" : themeId == 72 ? "vertical" : "reels" }_${themeId}`}
        className={`tb_fc_post_container ${isDisplay ? "display" : "widget"}`} ref={this.sliderHeight} 
        style={{ paddingTop: `${topMargin}px`, height : (row == 1) && themeId == 71 || postData.postData.length < 15 ? '' : themeId == 70 ? `auto` : `calc(100vh - ${(themeHeight)}px)`  }}>
          <SilderCom key={wHeight} { ...{inputRefs: this.inputRefs, inputName, themeId, row, numberOfCoumn, manageItemCount, slideDuration, mobileColumn, postData, trimContent, firstSildeData, secondSildeData,  completeDataObject, themeHeight, parentID, wall, postSizeInPx, clickToShowPopUp, onClickToCTA, type: themeId == 71 && isDisplay && row == 2 ? 2 :1, isDisplay, postData:postData.postData, intractiveData}} requestData={this.requestData}/>
          {
            !isDisplay ?
            <>
            <div
                className="splide__arrows splide__arrows--ltr tb_fc_arrow_wrapper_"
                role="navigation"
                aria-label="Slider Navigation Wrapper"
              >
                <div
                  role="navigation"
                  className="tb_fc_arrow splide__arrow arrow-left arrow tb_fc_arrow_left__ tb__icon tb-arrow-left-alt"
                  aria-labelledby="Navigation Previous Slide"
                >
                  {" "}
                </div>
                <div
                  role="navigation"
                  className="tb_fc_arrow splide__arrow arrow-right arrow tb_fc_arrow_right__ tb__icon tb-arrow-right-alt"
                  onClick={hasMoreData ? this.requestData : null}
                  aria-labelledby="Navigation Next Slide"
                >
                  {" "}
                </div>
              </div>
            </> : null
          }
      </div></div>  : null 
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getDataNextSteps: (
      wallID,
      timeStamp,
      postCount,
      networkId,
      after,
      postData,
      heightEvent,
      updateInStateNow
    ) =>
      dispatch(
        getDataNextSteps(
          wallID,
          timeStamp,
          postCount,
          networkId,
          after,
          postData,
          heightEvent,
          updateInStateNow
        )
      ),
  };
};

export default connect(null, mapDispatchToProps)(FallingColumns);