import React, { PureComponent } from "react";
import AuthorDetails from "../../Elements/AuthorDetails";
import CTAButton from "../../Elements/CTA/CtaButton";
import Rating from "../../Elements/Rating/Rating";
import { PostTrimContent } from "../../../../utils";
import PostContentDisplay from "../../Elements/PostContent/PostContentDisplay";
import Media from "../../Elements/Media";
import InteractiveEmoji from "../../Elements/InteractiveEmoji";

const trim_content_length = 140;

export default class Card extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { swiper: 0 };
    this.mediaHeight = React.createRef();
  }

  componentDidMount() {
     this.getSwiper();

     window.addEventListener("resize", () => {
         this.getSwiper()
     })

  }

  componentDidUpdate(pre, prevState){
      if(this.props.wall.ThemeRule.themeId!= pre.wall.ThemeRule.themeId) this.getSwiper()
   }

  getSwiper=()=>{

     const swiperRow = document.getElementById(`swiper-row`);

        setTimeout(() => {
            if (swiperRow) {
              const swiperHeight = swiperRow.clientHeight;
              const swiperprecnet = (swiperHeight*20/100);
              this.setState({ swiper: swiperHeight-swiperprecnet });
          }
        }, 10);
   }

  render() {
    const { itemData, autoplay, cardRef, clickToShowPopUp, itemIndex, wall, ownerId, onClickToCTA, themeHeight, row, intractiveData} = this.props;

    const { ThemeRule, Personalization } = wall;
    const interactive = wall.Personalization.interactive;
 
    const { themeId } = ThemeRule;
    const { swiper } = this.state;

    const mediaType =
      itemData.type === 2 ||
      itemData.type === 3 ||
      itemData.type === 4 ||
      itemData.type === 5
        ? true
        : false;
    const rating = itemData.rating && itemData.rating > 0 ? true : false;

    const cardSize = {
      padding: ThemeRule.padding / 2,
      height: [71].includes(themeId) && row == 2 ? `50vh` : "",
    };

    const cardStyle = {
      backgroundColor: ThemeRule.cardColor,
      padding: ![67, 68].includes(ThemeRule.themeId)
        ? [73].includes(ThemeRule.themeId)
          ? 0
          : ThemeRule.spacing
        : ThemeRule.spacing,
      borderRadius: ThemeRule.borderRadius,
    };

    const heightMange =
      ThemeRule.themeId == 70 && !itemData.file && !rating
        ? `calc(100vh - ${themeHeight + 100}px)`
        : null;

    const contentClass =
      itemData.rating > 0 ? "tb_fc_rating_content" : "tb_fc_content";
    const noMediaClass = itemData.type === 1 ? "tb_fc_text_post" : "";
    const textDecorate =
      itemData.type === 1 && Personalization.textDecorate
        ? "tb_fc_text_decoration"
        : "";
    const ctaActive =
      Personalization?.cta.ctaStatus || itemData?.cta?.status ? true : false;
    const postTextContent = ThemeRule.trimcontent
      ? PostTrimContent(itemData.content, trim_content_length)
      : itemData.content;

    const postAuthor = (ThemeRule.postAuthor && !itemData.author.isInstaUser) ? true : false;
    const network = itemData.network;

    return ( <>

   { !ThemeRule.hideContent ||  itemData.postFileNew ||  rating ?
       <div
        item-id={itemData.id}
        id={`tb-hc-post-${itemData.id}`}
        className={`tb_fc_post_wrapper ${
          !itemData.filename ? "textPostCard" : ""
        }  ${
          !itemData.file && !rating && ThemeRule.themeId == 70
            ? "snapHeight"
            : ""
        } ${interactive ? 'intractive_on' : ''}`}
        onClick={clickToShowPopUp(itemIndex, itemData)}
        aria-label="Wrapper"
        style={cardSize}
        ref={cardRef}>
        <div className="tb_fc_post_in" style={cardStyle} aria-label="Card Post">
          {mediaType ? (
            <div
              className="tb_fc_post_media_wrapp"
              role="img"
              aria-label="Media Wrapper"
            >
              <Media
                ThemeRule={ThemeRule}
                themeHeight={themeHeight}
                itemData={itemData}
                wall={wall}
                autoplayVideo={autoplay}
              />
            </div>
          ) : null}

          {rating ? (
            <div className={`tb_fc_rating__`} style={{ height: heightMange, aspectRatio : !itemData.file && ThemeRule.themeId == 70 ?  '1/1' : 'auto' }}>
              <Rating rating={itemData.rating} network={itemData.network} />
            </div>
          ) : null}


          { !ThemeRule.captionStatus ?  (
            <>
              <div
                className={`tb_fc_contant_wrapper ${noMediaClass}`}
                aria-label="Post Content Wrapper"
              >
                {  (postAuthor || ThemeRule.postTime || (network.id && ThemeRule.socialAction) ) ?  
                  <AuthorDetails
                    ownerId={ownerId}
                    postTime={itemData.createdAt}
                    author={itemData.author}
                    ThemeRule={ThemeRule}
                    rating={itemData.rating}
                    postData={itemData}
                  /> : null }
                  
                {ctaActive ? (
                  <div className="tb_fc_post_cta" aria-label="CTA Wrapper">
                    <CTAButton
                      ctaClass={"tb_fc_post_cta_btn"}
                      cta={Personalization.cta}
                      postCta={itemData.cta}
                      item={itemData}
                      onClickToCTA={onClickToCTA}
                    />{" "}
                  </div>
                ) : (
                  ""
                )}

                {!ThemeRule.hideContent ? (
                  <div
                    className={`tb_fc_content_wrapper__`}
                    aria-label="Content Wrapper"
                    style={{
                      height: ThemeRule.themeId != 70 ? heightMange : swiper && !itemData.file && !rating && ThemeRule.themeId == 70 ? (swiper-10) : 'auto',
                    }}
                  >
                     <PostContentDisplay
                      contentClass={`${contentClass} ${textDecorate}`}
                      item={itemData}
                      content={postTextContent}
                      font={ThemeRule.font}
                      wall={wall}
                      ThemeRule={ThemeRule}
                      personalization={Personalization}
                      contentTitle={itemData.contentTitle}
                    />
                  </div>
                ) : null}
                
              </div>
              {
                !wall.isDisplay && interactive ?
                <InteractiveEmoji {...{ isCard: true, postId: itemData.id, intractiveData, userDetail: wall.UserDetail, wallId: wall.Wall, interactiveCount: itemData.interactiveCount, isDisplay: wall.isDisplay, interactive: interactive, interactiveStatus: wall.Personalization.interactiveStatus}} key={(Math.random() * itemData.id)}/> : null
              }
            </>
          ) : null}
        </div>
      </div> : null  } </>
    );
  }
}
